.search {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin: 12px 0;

  &__item {
    margin-right: 20px;

    &_last {
      width: 100px;
    }

  }

  &__label {
    font-family: 'PF DinDisplay Pro', Helvetica, Arial, sans-serif;
    font-style: normal;
    display: flex;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #969696;
    height: 18px;

    &_stretch {
      justify-content: space-between;
    }
  }

  &__label-text {
    font-family: 'PF DinDisplay Pro', Helvetica, Arial, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    border-bottom: 1px solid #969696;
    color: #969696;
  }

  &__input {
    font-family: 'PF DinDisplay Pro', Helvetica, Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    display: block;
    max-width: 170px;
    width: 100%;
    height: 44px;
    padding-left: 12px;
    border-radius: 4px;
    border: 1px solid #969696;

    &_main {
      height: 32px;
    }

    &:hover {
      border: 1px solid #333333;
    }

    &:focus {
      border: 1px solid #439B24;
    }

    &:hover::placeholder {
      color: #333333;
    }

    &::placeholder {
      color: #969696
    }

  }
}